<template>
  <section>
    <ButtonGroup id="is_identified" v-model:selected-item="is_identified" :titles="btnGroupIdentified" class="max-w-[500px]" />
    <div v-if="is_identified === 1">
      <PageTitle> Aniqlangan bolalar r`oyxati </PageTitle>
      <TableTeenagersForSelectWrapper
        v-model:selected-juvenile="selectedJuvenile"
        :update-juveniles-list="updateJuvenilesList"
        :fetch-juveniles-list-handler="getDeterminedJuvenilesList"
        @delete="deleteJuvenile"
        :isDelete="true"
      />
    </div>
    <div v-if="is_identified === 0">
      <PageTitle>Aniqlanmagan bolalar r`oyxati</PageTitle>
      <TableTeenagersForSelectWrapper
        v-model:selected-juvenile="selectedJuvenile.personal_info"
        :update-juveniles-list="updateJuvenilesList"
        :fetch-juveniles-list-handler="getNotDefinedJuvenilesList"
        @delete="deleteJuvenile"
        :isDelete="true"
      />
    </div>
    <section v-if="selectedJuvenile.id || selectedJuvenile.personal_info?.id" class="max-w-[600px] bg-white mb-[12px]">
      <PassportCard :passport-info="selectedJuvenile.personal_info" @click="selectJuvenileHandler" @canceled="deleteSelectedJuvenile" />
    </section>
  </section>
</template>

<script setup>
import TableTeenagersForSelectWrapper from './TableTeenagersForSelectWrapper';
import {defineEmits, ref} from 'vue';
import JuvenilesService from '../../services/juveniles.service';
import ButtonGroup from '../Common/ButtonGroup';
import {btnGroupIdentified} from '../../utils/buttonGroupTitles';
import PageTitle from '../LayoutItems/PageTitle';
import PassportCard from '../PassportCard';
import Swal from 'sweetalert2';
const emit = defineEmits(['select-juvenile']);

const is_identified = ref(btnGroupIdentified[0].type);
const updateJuvenilesList = ref(false);
const selectedJuvenile = ref({
  id: '',
  personal_info: null,
});

const selectJuvenileHandler = () => {
  console.log(selectedJuvenile.value);
  if (selectedJuvenile.value?.personal_info?.pinfl) {
    emit('select-juvenile', selectedJuvenile.value);
  } else {
    emit('select-juvenile', {
      id: selectedJuvenile.value.personal_info.id,
      personal_info: selectedJuvenile.value.personal_info,
    });
  }
};
const deleteSelectedJuvenile = () => {
  selectedJuvenile.value = {
    id: '',
    personal_info: null,
  };
};

const deleteJuvenile = (juvenile) => {
  Swal.fire({
    title: "Tizimdan mazkur bola to'g'risidagi ma'lumotlarni o'chirish",
    showCancelButton: true,
    showConfirmButton: false,
    showDenyButton: true,
    denyButtonText: 'Tasdiqlash',
    cancelButtonText: `Bekor qilish`,
  }).then((result) => {
    if (result.isDenied) {
      if (is_identified.value === 1) {
        delete_incomplete_juvenile(juvenile.id);
      } else {
        delete_unidentified_juvenile(juvenile.id);
      }
    }
  });
};

const {getDeterminedJuvenilesList, getNotDefinedJuvenilesList, delete_incomplete_juvenile, delete_unidentified_juvenile} = JuvenilesService;
</script>

<style scoped></style>
